/* ----------- DESKTOP LARGE---------- */
@include device(big-desktop){
    .wrapper {
    display: grid;
    grid-template-columns: 1fr 1120px 160px 1fr;
    grid-template-rows: [header-start] 143px [header-end content-start] auto [content-end footer-start] 45px [footer-end];
    grid-template-areas:
            "header header header header"
            "... content sidebar ..."
            "footer footer footer footer";
    }
}